@use "variables" as v;

h1,
h2,
h3 {
  font-family: v.$fuente_principal;
  margin: 0 0 calc(v.$separacion / 2) 0;
  font-weight: 900;
  color: v.$rosa;
  text-align: center;
}

h1 {
  font-size: 4rem;
}
h2 {
  font-size: 3.6rem;
}
h3 {
  font-size: 3rem;
  text-align: center;
}
p {
  color: v.$negro;
  font-size: 2rem;
  margin-bottom: 5rem;
}
.nombre-pagina {
  text-align: center;
  font-weight: v.$bold;
  margin: 4rem 0;
}
.descripcion-pagina {
  text-align: center;
}
