/** Media Queries **/
@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.imagen {
  height: 30rem;
  background-image: url(../img/portada.jpg);
  background-size: cover;
  background-position: center center;
}

@media (min-width: 768px) {
  .imagen {
    height: auto;
  }
}
.app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}

@media (min-width: 768px) {
  .app {
    overflow: scroll;
  }
}
.barra-servicios {
  margin-bottom: 5rem;
}

@media (min-width: 768px) {
  .barra-servicios {
    display: flex;
    justify-content: space-between;
  }
}
.barra-servicios .boton {
  margin: 0 0 1rem 0;
}

.seccion {
  padding: 5rem 0;
  display: none;
}

.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.barra {
  margin: 5rem 0;
  font-weight: 700;
  text-align: center;
  font-family: "Lugrasimo", cursive;
}

@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .barra .boton {
    background-color: rgb(127, 85, 115);
    padding: 1.5rem 4rem;
    color: #ffffff;
    margin-top: 2rem;
    font-size: 2rem;
    font-weight: 400;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: 0.3s;
    text-align: center;
    display: block;
    width: 100%;
    margin: 5rem 0;
    border-radius: 2rem;
    margin: 0;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
@media (min-width: 768px) {
  .barra .boton:hover {
    background-color: #604157;
    cursor: pointer;
  }
}
.barra .boton {
  background-color: rgb(127, 85, 115);
  color: #ffffff;
  margin: 1.4rem;
}

.barra p {
  margin: 0;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}

@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  background-color: rgba(249, 198, 229, 0.518);
  text-align: center;
  border-radius: 1rem;
  transition: transform 300ms;
}

.listado-servicios .servicio.seleccionado {
  background-color: rgb(127, 85, 115);
}

.listado-servicios .servicio.seleccionado p {
  color: #ffffff;
}

.listado-servicios .servicio.seleccionado .precio-servicio {
  color: rgb(252, 180, 248);
}

.listado-servicios .servicio:hover {
  transform: scale(1.1);
}

.listado-servicios .servicio p {
  margin: 2rem;
}

.listado-servicios .servicio .precio-servicio {
  color: rgba(100, 66, 124, 0.7176470588);
  font-weight: 700;
  font-size: 2.5rem;
}

.contenido-resumen p {
  margin-bottom: 1rem;
}

.contenido-resumen span {
  color: rgb(150, 85, 130);
  font-weight: 700;
  font-size: 2.4rem;
}

.contenido-resumen button {
  margin-top: 5rem;
}

.contenedor-servicio {
  padding: 3rem 0;
  border-bottom: 1px solid rgb(127, 85, 115);
}

.contenedor-servicio:last-of-type {
  margin-bottom: 5rem;
}

.contenedor-servicio:first-of-type {
  border-top: 1px solid rgb(127, 85, 115);
}

.citas, .servicios {
  list-style: none;
  padding: 0;
}

.citas li, .servicios li {
  padding: 1rem;
  border-top: 2px solid rgb(127, 85, 115);
}

.citas li p, .servicios li p {
  font-weight: 700;
  color: rgb(150, 85, 130);
}

.citas li span, .servicios li span,
.citas li .servicio,
.servicios li .servicio {
  font-weight: 400;
  color: #1a1b15;
}

.citas li span span, .servicios li span span,
.citas li .servicio span,
.servicios li .servicio span {
  color: rgba(100, 66, 124, 0.7176470588);
  font-weight: 700;
}

.citas li:hover, .servicios li:hover {
  background-color: rgba(249, 198, 229, 0.518);
}

.servicios .acciones {
  margin: 0;
  justify-content: space-around;
}

.servicios .boton,
.servicios .boton-eliminar {
  margin: 0;
}

